// Setting colors
$black-color: #31333b;
$white-color: #FFFFFF;

// import Fonts
@font-face {
    font-family: "Futura_Book";
    src: url("../../fonts/futura_book.ttf");
}

// Mixins
@mixin decoration-none {
    text-decoration: none;
    border: none ;
    outline: none;
}

@mixin align-middle {
    position: fixed;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
}

@mixin box-shadow-input {
    box-shadow: 0 6px 8px rgba(102,119,136,.03), 0 1px 2px rgba(102,119,136,.3);
}

@import './reset';
@import './App';
@import './Navigation';
@import './Create';
@import './Edit';
@import './PersonList';